<template>
  <v-container>
    <div class="display-1 mb-4">Nombre de la intención: {{intent.displayName}}</div>
    <v-text-field class="intentName" label="Nombre de la intención" v-model="intent.displayName"></v-text-field>
    <h3>Frases de entrenamiento</h3>
    <v-text-field
      prepend-inner-icon="mdi-format-quote-close"
      placeholder="Añade una frase de entrenamiento"
      v-model="newTrainingPhrase"
      @keyup.enter="addTrainingPhrase(intent.trainingPhrases,newTrainingPhrase)"
    ></v-text-field>
    <div
      v-for="(trainingPhrase,trainingIndex) in intent.trainingPhrases"
      :key="'part'+trainingIndex"
    >
      <!-- {{trainingPhrase}} -->
      <v-layout align-center row v-for="(phrase,phraseIndex) in trainingPhrase.parts">
        <v-flex xs1 class="text-center">{{trainingIndex+1}}</v-flex>
        <v-flex xs11>
          <v-text-field
            append-outer-icon="mdi-delete"
            @click:append-outer="deleteTrainingPhrase(phraseIndex,trainingPhrase.parts)"
            prepend-inner-icon="mdi-format-quote-close"
            v-model="phrase.text"
          ></v-text-field>
        </v-flex>
      </v-layout>
    </div>
    <v-divider class="my-7"></v-divider>
    <h3>Respuestas</h3>
    <draggable @end="onChange" handle=".handle" v-model="intent.messages" :animation="300">
      <transition-group>
        <div v-for="(message,i) in intent.messages" :key="'tab'+i">
          <div v-if="message.hasOwnProperty('text')">
            <v-card class="mb-3">
              <v-toolbar class="handle" color="primary" dark>
                <v-toolbar-title>Mensaje multiple</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="deleteResponse(i)">Eliminar</v-btn>
              </v-toolbar>
              <v-card-text class="px-8">
                <v-layout align-center row v-for="(text,textIndex) in message.text.text">
                  <v-flex xs1 class="text-center">{{textIndex+1}}</v-flex>
                  <v-flex xs11>
                    <v-textarea
                      append-outer-icon="mdi-delete"
                      @click:append-outer="deleteResponseVariant(textIndex,message.text.text)"
                      rows="1"
                      auto-grow
                      v-model="message.text.text[textIndex]"
                    ></v-textarea>
                  </v-flex>
                </v-layout>
                <v-layout align-center row>
                  <v-flex xs1 class="text-center">*</v-flex>
                  <v-textarea
                    clearable
                    @keyup.enter="addResponseVariante(i,message.text.text,responseVariant[i])"
                    placeholder="Añadir una variante a la respuesta"
                    v-model="responseVariant[i]"
                    rows="1"
                    auto-grow
                  ></v-textarea>
                  <v-flex xs11></v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </div>
          <div v-if="message.hasOwnProperty('payload')">
            <v-card class="mb-3">
              <v-toolbar class="handle" color="primary" dark>
                <v-toolbar-title>Rich Message</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="deleteResponse(i)">Eliminar</v-btn>
              </v-toolbar>
              <!-- <p>{{structToJson(message.payload)}}</p> -->
              <rich-message
                v-if="visible==1"
                @onSave="updateLocalData"
                ref="richMessageRef"
                :index.sync="i"
                :data.sync="message.payload"
              ></rich-message>
            </v-card>
          </div>
        </div>
      </transition-group>
    </draggable>
    <v-btn color="error" text :to="{name:'intent'}">Atras</v-btn>
    <v-btn class="ml-3" color="success" @click="updateIntent(intent)">Guardar</v-btn>
    <v-menu offset-y>
      <template v-slot:activator="{ on }">
        <v-btn class="ml-3" color="primary" v-on="on">Añadir respuesta</v-btn>
      </template>
      <v-list>
        <v-list-item v-for="(item, index) in options" :key="index" @click="createResponse(item.id)">
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-container>
</template>

<script>
import axios from "axios";
import draggable from "vuedraggable";
import { generateObjectId } from "../../tools/objectIdGenerator.js";
import {
  structProtoToJson,
  jsonToStructProto
} from "../../tools/structFunctions";
import {
  simpleMessage,
  buttonMessage,
  carruselButton,
  quickReply
} from "../../tools/richMessageTemplate";
export default {
  components: {
    draggable
  },
  data() {
    return {
      newTrainingPhrase: "",
      responseVariant: [],
      visible: 1,
      options: [
        { id: 1, title: "Mensaje simple" },
        { id: 2, title: "Boton" },
        { id: 3, title: "Menú carrusel" },
        { id: 4, title: "Quick Replies" }
      ],
      intent: {}
    };
  },
  mounted() {
    this.getInitialData();
    console.log("intent recibido: ", this.intent);
  },
  methods: {
    deleteTrainingPhrase(phraseIndex, trainingPhrases) {
      trainingPhrases.splice(phraseIndex, 1);
    },
    addTrainingPhrase(trainingPhrases, newTrainingPhrase) {
      trainingPhrases.push({
        parts: [
          {
            text: newTrainingPhrase,
            entityType: "",
            alias: "",
            userDefined: false
          }
        ],
        name: "be6f229c-44c2-496f-929d-7f79c7395b9d",
        type: "EXAMPLE",
        timesAddedCount: 0
      });
      this.newTrainingPhrase = "";
    },
    deleteResponseVariant(index, messages) {
      messages.splice(index, 1);
    },
    addResponseVariante(index, responses, responseVariant) {
      console.log("llegaron estas respuestas: ", responses);
      responses.push(responseVariant);
      this.responseVariant[index] = "";
      console.log("añadiendo variante a la respuesta: ", index);
    },
    onChange() {
      console.log("hubo un cambio");
      this.visible = 0;
      setTimeout(() => {
        this.visible = 1;
      }, 500);
    },
    getInitialData() {
      this.intent = this.$store.state.intents.find(element =>
        element.name.includes(this.$route.params.id)
      );
      this.intent.messages.forEach(element => {
        this.responseVariant.push("");
      });
    },
    updateLocalData({ data, index }) {
      console.log("nos quedamos aca");
      console.log(
        "anteriormente tenias: ",
        this.structToJson(this.intent.messages[index].payload, index).facebook
          .attachment.payload
      );
      this.intent.messages[index].payload = this.jsonToStruct(data);

      console.log(
        "datos del padre actualizado con: ",
        data.facebook.attachment.payload
      );
    },
    updateIntent(newIntent) {
      this.$store.dispatch("showOverlay", {
        active: true,
        text: "Guardando"
      });
      if (this.$refs.richMessageRef) {
        this.$refs.richMessageRef.forEach(richMessage => {
          let index = richMessage.index;
          let newRichMessage = richMessage.jsonData;
          newIntent.messages[index].payload = this.jsonToStruct(newRichMessage);
        });
      }

      axios
        .put("/api/chatbot/agent/intents/update", { newIntent })
        .then(res => {
          if (res.data.ok) {
            console.log("intent actualizado con exito");
            console.log(res.data);
            this.$store.dispatch("showSnackbar", {
              text: "Cambios guardados con éxito",
              color: "success"
            });
          } else {
            // error msg
            console.error(res.data);
            this.$store.dispatch("showSnackbar", {
              text: "Algo salió mal",
              color: "error"
            });
          }
        })
        .catch(error => {
          console.log(error);
          this.$store.dispatch("showSnackbar", {
            text: "Algo salió mal",
            color: "error"
          });
        })
        .finally(() => {
          this.$store.dispatch("showOverlay", {
            active: false,
            text: ""
          });
        });
    },
    createResponse(id) {
      let jsonIntent = this.intent;
      switch (id) {
        case 1:
          console.log("para pushear: ", jsonIntent);
          jsonIntent.messages.push(simpleMessage());
          break;
        case 2:
          console.log("seleccionado: ", buttonMessage());
          jsonIntent.messages.push(buttonMessage());
          break;
        case 3:
          console.log("seleccionado: ", carruselButton());
          jsonIntent.messages.push(carruselButton());
          break;
        case 4:
          console.log("seleccionado: ", carruselButton());
          jsonIntent.messages.push(quickReply());
          break;

        default:
          break;
      }
      this.intent = jsonIntent;
    },
    deleteResponse(id) {
      console.log("se eliminara: ", id);
      this.intent.messages.splice(id, 1);
      // console.log("Eliminando: ", id);
    },
    jsonToStruct(json) {
      return jsonToStructProto(json);
    },
    structToJson(struct) {
      return structProtoToJson(struct);
    }
  }
  // computed: {
  //   simpleText() {
  //     return this.intent.message.text.text
  //   }
  // },
};
</script>

<style scoped>
.intentName {
  font-size: 20px;
}
</style>