<template>
  <div>
    <v-dialog v-model="dialog" width="800">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Editar</v-btn>
      </template>
      <v-card>
        <v-toolbar color="secondary" dark>
          <v-toolbar-title>Mensaje tipo Quick Reply</v-toolbar-title>
        </v-toolbar>
        <v-container>
          <v-layout justify-center>
            <v-flex sm11>
              <!-- <v-text-field label="Mensaje" v-model="buttonMessage"></v-text-field> -->
              <p>Mensaje que acompaña las opciones del quick reply</p>
              <v-text-field outlined v-model="quick_replies_message.text"></v-text-field>
              <div v-for="(reply,i) in quick_replies">
                <v-layout row align-center>
                  <v-flex xs6>
                    <v-text-field
                      outlined
                      :label="`Texto del quick reply ${i+1}`"
                      v-model="reply.title"
                    ></v-text-field>
                    <v-text-field
                      outlined
                      :label="`Mensaje de emparejamiento (Para dialogflow)`"
                      v-model="reply.payload"
                    ></v-text-field>
                    <v-text-field
                      outlined
                      :label="`URL de la imagen ${i+1}`"
                      v-model="reply.image_url"
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs6>
                    <v-card class="ml-3 pa-3">
                      <div>
                        <strong>Vista previa:</strong>
                      </div>

                      <v-layout justify-center>
                        <v-btn class="text-center mr-4" rounded outlined color="primary">
                          <v-img
                            class="imgButton"
                            :src="reply.image_url"
                            aspect-ratio="1.7"
                            contain
                          ></v-img>
                          {{reply.title}}
                        </v-btn>
                      </v-layout>
                    </v-card>
                    <v-layout justify-center>
                      <v-btn color="error" outlined @click="deleteReply(i)">Eliminar</v-btn>
                    </v-layout>
                  </v-flex>
                </v-layout>
                <div class="line"></div>
              </div>
              <v-btn color="primary" outlined @click="addQuickReply">Añadir otro botón (MAX 13)</v-btn>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-dialog>
    <v-layout justify-center>
      <v-flex sm6>
        <v-card class="ml-3 pa-3">
          <div>
            <strong>Vista previa del rich message:</strong>
          </div>
          <v-divider></v-divider>
          <p>{{quick_replies_message.text}}</p>
          <v-btn
            v-for="(reply,i) in quick_replies"
            class="text-center"
            rounded
            outlined
            color="primary"
          >{{reply.title}}</v-btn>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  props: ["data"],
  data() {
    return {
      dialog: false
    };
  },
  computed: {
    quick_replies() {
      return this.data.facebook.quick_replies;
    },
    quick_replies_message() {
      return this.data.facebook;
    }
  },
  methods: {
    addQuickReply() {
      this.data.facebook.quick_replies.push({
        content_type: "text",
        title: "reply",
        payload: "reply_payload",
        image_url: ""
      });
    },
    deleteReply(index) {
      this.data.facebook.quick_replies.splice(index, 1);
    }
  }
};
</script>

<style scoped>
.line {
  border-bottom: 1px solid #c1c1c1;
  margin-top: 3px;
  margin-bottom: 30px;
}
.imgButton {
  background-color: transparent; /* make the button transparent */
  background-repeat: no-repeat; /* make the background image appear only once */
  background-position: 0px 0px; /* equivalent to 'top left' */
  border: none; /* assuming we don't want any borders */
  cursor: pointer; /* make the cursor like hovering over an <a> element */
  height: 16px; /* make this the size of your image */
  padding-left: 16px; /* make text start to the right of the image */
  vertical-align: middle; /* align the text vertically centered */
}
</style>