<template>
  <div>
    <v-dialog v-if="rerender==false" persistent v-model="dialog" width="800">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Editar</v-btn>
      </template>
      <v-card>
        <v-toolbar color="primary" dark>
          <v-toolbar-title>Mensaje tipo generic</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="dialog=false;">
            <v-icon color="white">mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-container grid-list-lg>
          <v-tabs v-model="tab" background-color="primary" centered dark icons-and-text>
            <v-tabs-slider></v-tabs-slider>
            <v-tab v-for="(generic,i) in generics" :key="i" :href="'#tab-'+i">Pantalla {{i+1}}</v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item
              v-for="(generic,screenIndex) in generics"
              :key="screenIndex"
              :value="'tab-' + screenIndex"
            >
              <v-layout wrap align-center class="mb-4">
                <v-flex sm6>
                  <v-text-field label="titulo" v-model="generic.title"></v-text-field>
                </v-flex>
                <v-flex sm6>
                  <v-text-field label="descripcion" v-model="generic.subtitle"></v-text-field>
                </v-flex>
                <v-flex sm12>
                  <v-text-field clearable label="link imagen" v-model="generic.image_url"></v-text-field>
                </v-flex>
                <v-flex sm12>
                  <v-layout align-center>
                    <span class="body-1">Orden de aparición de la pantalla: {{screenIndex+1}}</span>
                    <v-dialog persistent v-model="dialog2" width="500">
                      <template v-slot:activator="{ on }">
                        <v-btn
                          v-on="on"
                          @click="showOrder(screenIndex+1)"
                          class="ml-3"
                          color="primary"
                        >modificar</v-btn>
                      </template>
                      <v-card>
                        <v-card-title>
                          <v-text-field
                            @keyup.enter="updateOrderScreen(screenIndex,orderScreen)"
                            v-model="orderScreen"
                            name="Orden"
                            label="Orden"
                            id="id"
                          ></v-text-field>
                        </v-card-title>
                      </v-card>
                    </v-dialog>
                  </v-layout>
                </v-flex>
              </v-layout>
              <strong>Botones</strong>
              <div v-for="(button,i) in generic.buttons" :key="'loop'+i">
                <span class="title">Boton {{i+1}}</span>
                <v-btn
                  small
                  color="error"
                  class="ml-4"
                  @click="deleteButton(screenIndex,i)"
                >Eliminar</v-btn>
                <v-text-field
                  label="texto del boton"
                  :counter="20"
                  :rules="buttonTextRules"
                  v-model="button.title"
                ></v-text-field>
                <p class="body-1">Tipo de botón</p>
                <v-radio-group
                  @change="updateBtnStructure(button.type,i,button,screenIndex)"
                  v-model="button.type"
                >
                  <v-radio label="POSTBACK" value="postback"></v-radio>
                  <v-radio label="ENLACE WEB" value="web_url"></v-radio>
                </v-radio-group>
                <v-text-field
                  v-if="button.type=='postback'"
                  v-model="button.payload"
                  clearable
                  outlined
                  label="Mensaje de emparejamiento"
                ></v-text-field>
                <v-text-field clearable v-else v-model="button.url" outlined label="Dirección URL"></v-text-field>
              </div>
              <v-layout justify-center>
                <v-flex sm11>
                  <v-layout row justify-center>
                    <v-flex xs6>
                      <v-card class="ml-3 pa-3">
                        <strong>Vista previa del rich message:</strong>
                        <v-img
                          v-if="generic.image_url"
                          :src="generic.image_url"
                          aspect-ratio="1.7"
                          contain
                        ></v-img>
                        <strong>{{generic.title}}</strong>
                        <p>{{generic.subtitle}}</p>
                        <div v-for="button in generic.buttons">
                          <v-btn color="primary" outlined block>{{button.title}}</v-btn>
                        </div>
                      </v-card>
                    </v-flex>
                  </v-layout>
                  <!-- <v-btn color="primary" outlined @click>Añadir otro botón (MAX 3)</v-btn> -->
                </v-flex>
              </v-layout>
            </v-tab-item>
          </v-tabs-items>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="success" @click="dialog=false">Guardar</v-btn>
          </v-card-actions>
        </v-container>
      </v-card>
    </v-dialog>
    <v-layout justify-center>
      <v-flex sm11>
        <v-layout row justify-center>
          <v-flex sm3 v-for="(generic,i) in generics" :key="i">
            <v-btn small color="error" @click="deleteGeneric(i)">Eliminar</v-btn>
            <v-card class="ml-3 pa-3">
              <strong>Vista previa del rich message:</strong>
              <v-img v-if="generic.image_url" :src="generic.image_url" aspect-ratio="1.7" contain></v-img>
              <strong>{{generic.title}}</strong>
              <p>{{generic.subtitle}}</p>
              <div v-for="button in generic.buttons">
                <v-btn color="primary" outlined block>{{button.title}}</v-btn>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
        <v-btn color="primary" outlined @click="addGeneric">Añadir otra pantalla</v-btn>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  props: ["data"],
  data() {
    return {
      dialog: false,
      dialog2: false,
      tab: null,
      orderScreen: 0,
      rerender: false,
      buttonTextRules: [
        v => !!v || "Nombre es requerido",
        v =>
          (v && v.length <= 10) ||
          "Si superas los 20 caracteres, las sobrantes aparecerán como '...'"
      ]
    };
  },
  mounted() {
    console.log("generic recibido: ", this.data);
  },
  computed: {
    generics() {
      return this.data.facebook.attachment.payload.elements;
    }
  },
  methods: {
    deleteGeneric(index) {
      this.generics.splice(index, 1);
    },
    updateOrderScreen(currentIndex, newIndex) {
      console.log("el indice de origen: ", currentIndex);
      console.log("el indice de destino: ", newIndex);
      this.dialog2 = false;
      let currentGeneric = JSON.parse(
        JSON.stringify(this.generics[currentIndex])
      );
      let objetiveGeneric = JSON.parse(
        JSON.stringify(this.generics[newIndex - 1])
      );
      this.generics[currentIndex] = objetiveGeneric;
      this.generics[newIndex - 1] = currentGeneric;
      this.rerender = true;
      setTimeout(() => {
        this.rerender = false;
      }, 300);
      this.tab = newIndex - 1;
      console.log("el indice del tab: ", this.tab + 1);
    },
    showOrder(index) {
      this.orderScreen = index;
    },
    updateBtnStructure(type, buttonIndex, button, screenIndex) {
      let postbackTemplate = {
        title: button.title,
        type: "postback",
        payload: ""
      };
      let webUrlTemplate = {
        title: button.title,
        type: "web_url",
        url: ""
      };
      if (type == "postback") {
        this.generics[screenIndex].buttons.splice(
          buttonIndex,
          1,
          postbackTemplate
        );
      } else {
        this.generics[screenIndex].buttons.splice(
          buttonIndex,
          1,
          webUrlTemplate
        );
      }
      console.log(
        "la nueva estructura es:",
        this.generics[screenIndex].buttons[buttonIndex]
      );
    },
    addGeneric() {
      console.log("anadiendo screen");
      this.data.facebook.attachment.payload.elements.push({
        title: "PANTALLA 01",
        image_url:
          "https://miro.medium.com/max/4000/1*m9IJdAYW04MYh75ivpwUNA.png",
        subtitle: "Descripcion de la pantalla 01",
        buttons: [
          { type: "postback", title: "boton 01", payload: "button1_payload" },
          { type: "postback", title: "boton 02", payload: "button2_payload" },
          { type: "postback", title: "boton 03", payload: "button3_payload" }
        ]
      });
    },
    deleteButton(screenIndex, buttonIndex) {
      this.generics[screenIndex].buttons.splice(buttonIndex, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
</style>