<template>
  <v-snackbar :timeout="3000" :color="colorSnackbar" v-model="snackbar">
    {{ $store.state.snackbar.text }}
    <v-btn dark text @click="$store.state.snackbar.active = false">Cerrar</v-btn>
  </v-snackbar>
</template>

<script>
export default {
  computed: {
    snackbar: {
      get() {
        return this.$store.state.snackbar.active;
      },
      set(newValue) {
        this.$store.state.snackbar.active = newValue;
      }
    },
    colorSnackbar() {
      return this.$store.state.snackbar.color;
    }
  }
};
</script>

