<template>
  <v-card v-if="jsonData" outlined class="pa-5">
    <strong>Rich message de tipo: {{getType(jsonData)}}</strong>
    <component
      @onSave="updateParent(jsonData,index)"
      :data="jsonData"
      :is="selectRichMessage(getType(jsonData))"
    ></component>
  </v-card>
</template>

<script>
import richButton from "../richMessages/richButton";
import generic from "../richMessages/generic";
import quickReply from "../richMessages/quickReply";
import { structProtoToJson } from "../../tools/structFunctions";
export default {
  props: ["data", "index"],
  data() {
    return {
      jsonData: null,
      components: [richButton, generic, quickReply]
    };
  },
  mounted() {
    this.jsonData = this.data;
    this.jsonData = this.structToJson(this.jsonData);
  },
  methods: {
    getType(template) {
      if (template.facebook.hasOwnProperty("attachment")) {
        return template.facebook.attachment.payload.template_type;
      }
      if (template.facebook.hasOwnProperty("quick_replies")) {
        return "quick_reply";
      }
    },
    selectRichMessage(type) {
      switch (type) {
        case "button":
          return this.components[0];
          break;
        case "generic":
          return this.components[1];
          break;
        case "quick_reply":
          return this.components[2];
          break;
        default:
          break;
      }
    },
    structToJson(struct) {
      return structProtoToJson(struct);
    },
    updateParent(data, index) {
      this.$emit("onSave", { data, index });
    }
  }
};
</script>

<style lang="scss" scoped>
</style>