<template>
  <div>
    <v-dialog persistent v-model="dialog" width="800">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Editar</v-btn>
      </template>
      <v-card>
        <v-toolbar color="primary" dark>
          <v-toolbar-title>Mensaje tipo botón</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="dialog=false;">
            <v-icon color="white">mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-container>
          <v-layout justify-center>
            <v-flex sm11>
              <v-text-field clearable label="Mensaje" v-model="buttonMessage"></v-text-field>
              <v-layout row align-center>
                <v-flex xs6>
                  <v-layout row wrap v-for="(button,i) in buttonsList">
                    <v-flex xs12>
                      <v-text-field
                        clearable
                        outlined
                        :counter="20"
                        :rules="buttonTextRules"
                        :label="`Texto del boton ${i+1}`"
                        v-model="button.title"
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        clearable
                        outlined
                        :label="`URL del boton ${i+1}`"
                        v-model="button.url"
                      ></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-flex xs6>
                  <v-card class="ml-3 pa-3">
                    <strong>Vista previa del rich message:</strong>
                    <p>{{buttonMessage}}</p>
                    <div v-for="button in buttonsList">
                      <v-btn block color="primary" outlined>{{button.title}}</v-btn>
                    </div>
                  </v-card>
                </v-flex>
              </v-layout>
              <v-card-actions>
                <v-btn color="primary" outlined @click="addButton">Añadir otro botón (MAX 3)</v-btn>
                <v-btn color="success" class="ml-3" outlined @click="dialog=false;save()">Guardar</v-btn>
              </v-card-actions>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-dialog>
    <v-layout justify-center>
      <v-flex sm6>
        <v-card class="ml-3 pa-3">
          <strong>Vista previa del rich message:</strong>
          <p>{{buttonMessage}}</p>
          <div v-for="button in buttonsList">
            <v-btn block color="primary" outlined>{{button.title}}</v-btn>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  mounted() {},
  props: ["data"],
  data() {
    return {
      dialog: false,
      buttonTextRules: [
        v => !!v || "Nombre es requerido",
        v =>
          (v && v.length <= 10) ||
          "Si superas los 20 caracteres, las sobrantes aparecerán como '...'"
      ]
    };
  },
  computed: {
    buttonsList() {
      return this.data.facebook.attachment.payload.buttons;
    },
    buttonMessage: {
      get: function() {
        return this.data.facebook.attachment.payload.text;
      },
      // setter
      set: function(newValue) {
        this.data.facebook.attachment.payload.text = newValue;
      }
    }
  },
  methods: {
    addButton() {
      this.data.facebook.attachment.payload.buttons.push({
        type: "web_url",
        title: "ejemplo",
        url:
          "https://developers.facebook.com/docs/messenger-platform/send-messages/template/list"
      });
    },
    save() {
      this.$emit("onSave");
    }
  }
};
</script>

<style lang="scss" scoped>
</style>