import {
    jsonToStructProto
} from "./structFunctions";

const simpleMessage = () => {
    let template = {
        message: "text",
        platform: "PLATFORM_UNSPECIFIED",
        text: {
            text: ["Nuevo mensaje"]
        }
    }
    return template;
}
const quickReply = () => {
    let template = {
        message: "payload",
        platform: "PLATFORM_UNSPECIFIED",
        payload: jsonToStructProto({
            facebook: {
                text: "¿Aceptas?",
                quick_replies: [{
                        content_type: "text",
                        title: "si",
                        payload: "si_intent_payload",
                        image_url: ""
                    },
                    {
                        content_type: "text",
                        title: "no",
                        payload: "no_intent_payload",
                        image_url: ""
                    }
                ]
            }
        })
    }
    return template;
}
const buttonMessage = () => {
    let template = {
        message: "payload",
        payload: jsonToStructProto({
            facebook: {
                attachment: {
                    type: "template",
                    payload: {
                        template_type: "button",
                        text: "Este mensaje va junto al boton",
                        buttons: [{
                            type: "web_url",
                            title: "edita",
                            url: "https://developers.facebook.com/docs/messenger-platform/send-messages/template/list"
                        }]
                    }
                }
            }
        }),
        platform: "PLATFORM_UNSPECIFIED"
    }
    return template;
}
const carruselButton = () => {
    let template = {
        message: "payload",
        payload: jsonToStructProto({
            facebook: {
                attachment: {
                    type: "template",
                    payload: {
                        template_type: "generic",
                        elements: [{
                            title: "PANTALLA 01",
                            image_url: "http://4.bp.blogspot.com/-_QCxfa0Jjv4/U1h7FRoJnAI/AAAAAAAAAYg/lbU-uWs5T-M/s1600/cpa-school-test.png",
                            subtitle: "Descripcion de la pantalla 01",
                            buttons: [{
                                type: "postback",
                                title: "boton 01",
                                payload: "button1_payload"
                            }, {
                                type: "postback",
                                title: "boton 02",
                                payload: "button2_payload"
                            }, {
                                type: "postback",
                                title: "boton 03",
                                payload: "button3_payload"
                            }]
                        }]
                    }
                }
            }
        }),
        platform: "PLATFORM_UNSPECIFIED"
    }

    return template;
}


export {
    simpleMessage,
    buttonMessage,
    carruselButton,
    quickReply
}