<template>
  <v-container grid-list-xs>
    <v-layout column align-center>
      <v-card width="500">
        <v-toolbar color="primary">
          <v-toolbar-title class="white--text" color="primary">Inicio de sesión</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-form ref="form" v-model="valid" :lazy-validation="lazy">
            <v-text-field
              v-model="user.first_name"
              :counter="20"
              :rules="nameRules"
              label="Nombres"
              required
            ></v-text-field>
            <v-text-field
              v-model="user.last_name"
              :counter="20"
              :rules="nameRules"
              label="Apellidos"
              required
            ></v-text-field>
            <v-text-field v-model="user.email" :rules="emailRules" label="E-mail" required></v-text-field>
            <v-text-field
              :type="show1 ? 'text' : 'password'"
              :append-icon="show1 ? 'visibility' : 'visibility_off'"
              @click:append="show1 = !show1"
              v-model="user.password"
              :counter="12"
              :rules="nameRules"
              label="contraseña"
              required
            ></v-text-field>
            <v-btn color="primary" @click="registerUser(user)">Registrar</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    user: {
      first_name: "",
      last_name: "",
      email: "",
      password: ""
    },
    show1: false,
    valid: true,
    name: "",
    nameRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 20) || "Nombre debe ser menos de 15 caracteres"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+\..+/.test(v) || "E-mail debe ser válido"
    ],
    lazy: false
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    registerUser(user) {
      this.$store
        .dispatch("register", user)
        .then(res => {
          console.log(res);
          this.$store.dispatch("showSnackbar", res);
          this.$router.push({ name: "login" });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
</style>