<template>
  <v-container grid-list-xs>
    <v-layout column align-center>
      <v-card width="500">
        <v-toolbar color="primary">
          <v-toolbar-title class="white--text" color="primary">Inicio de sesión</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-form ref="form" v-model="valid" :lazy-validation="lazy">
            <v-text-field v-model="user.email" :rules="emailRules" label="E-mail" required></v-text-field>
            <v-text-field
              :type="show1 ? 'text' : 'password'"
              :append-icon="show1 ? 'visibility' : 'visibility_off'"
              @click:append="show1 = !show1"
              v-model="user.password"
              :counter="10"
              :rules="nameRules"
              label="contraseña"
              required
            ></v-text-field>
            <v-btn color="primary" @click="login(user.email,user.password)">Ingresar</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import localStorage from "localStorage";
export default {
  data: () => ({
    show1: false,
    user: {
      email: "",
      password: ""
    },
    valid: true,
    name: "",
    nameRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 10) || "Name must be less than 10 characters"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ],
    lazy: false
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    login(email, password) {
      this.$store
        .dispatch("login", { email, password })
        .then(res => {
          this.$store.dispatch("showSnackbar", { res, color: "success" });
          console.log("mensaje del servidor: ", res);
          this.$router.push({ name: "intent" });
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
</style>