<template>
  <v-container grid-list-xs>
    <v-layout class="mb-4" align-center>
      <span class="display-1">Gestión del menú persistente</span>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn v-on="on" class="mr-4" color="primary">Añadir opción</v-btn>
        </template>
        <v-list>
          <v-list-item @click="addLinkButton()">
            <v-list-item-title>De tipo enlace</v-list-item-title>
          </v-list-item>
          <v-list-item @click="addPostbackButton()">
            <v-list-item-title>De tipo postback</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-layout>
    <div v-for="(button,i) in buttons" :key="i">
      <v-card outlined class="mb-4 pa-5">
        <v-layout justify-space-between>
          <span class="title mb-3">Configuración del botón {{i+1}}</span>
          <v-btn color="error" @click="deleteButton(i)">Eliminar</v-btn>
        </v-layout>
        <v-text-field outlined label="Nombre del botón" v-model="button.title"></v-text-field>
        <p class="body-1">Tipo de botón</p>
        <v-radio-group v-model="button.type">
          <v-radio label="POSTBACK" value="postback"></v-radio>
          <v-radio label="ENLACE WEB" value="web_url"></v-radio>
        </v-radio-group>
        <v-text-field
          v-model="button.payload"
          outlined
          label="Mensaje de emparejamiento"
          v-if="button.type=='postback'"
        ></v-text-field>
        <v-text-field v-else v-model="button.url" outlined label="Dirección URL"></v-text-field>
      </v-card>
    </div>
    <v-btn color="success" @click="save(buttons)">Guardar</v-btn>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      buttons: [
        {
          type: "postback",
          title: "Fun news",
          payload: "FUN_NEWS"
        },
        {
          title: "View Website",
          type: "web_url",
          url: "http://www.myapple.com"
        }
      ],
      radioGroup: 1
    };
  },
  mounted() {
    this.initialData();
  },
  methods: {
    initialData() {
      axios
        .get("/api/chatbot/agent/persistantmenu/list")
        .then(res => {
          if (res.data.ok) {
            console.log(res.data);
            this.buttons = res.data.payload;
          } else {
            // error msg
            console.error(res.data);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    addLinkButton() {
      this.buttons.push({
        title: "View Website",
        type: "web_url",
        url: "http://www.myapple.com"
      });
    },
    addPostbackButton() {
      this.buttons.push({
        type: "postback",
        title: "Nuevas noticias",
        payload: "FUN_NEWS"
      });
    },
    save(buttons) {
      this.$store.dispatch("showOverlay", true);
      axios
        .post("/api/chatbot/agent/persistantmenu", { buttons: buttons })
        .then(res => {
          if (res.data.ok) {
            console.log(res.data);
          } else {
            // error msg
            console.error(res.data);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.$store.dispatch("showOverlay", false);
        });
    },
    deleteButton(index) {
      this.buttons.splice(index, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
</style>