<template>
  <v-container>
    <div class="display-1">Gestión de Agencias</div>
    <v-divider></v-divider>
    <v-data-table
      :search="search"
      :headers="headers"
      :items="agencies"
      sort-by="calories"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>Listado</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-dialog persistent v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="primary" dark class="mb-2" v-on="on">Agregar</v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container grid-list-md>
                  <v-layout row wrap align-center>
                    <v-flex xs12 sm12>
                      <v-text-field v-model="editedItem.agency_name" label="Nombre de la agencia"></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-select
                        :items="regions"
                        v-model="selectedRegion"
                        item-text="name"
                        label="Listado de regiones"
                        item-value="id"
                      ></v-select>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-textarea v-model="editedItem.schedule" label="Horarios"></v-textarea>
                    </v-flex>
                    <v-flex xs12 sm12>
                      <v-text-field v-model="editedItem.address" label="Dirección"></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm12>
                      <v-text-field v-model="editedItem.reference" label="Referencia"></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">CANCELAR</v-btn>
                <v-btn color="success" @click="save">GUARDAR</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.action="{ item }">
        <v-icon color="success" small class="mr-2" @click="editItem(item)">edit</v-icon>
        <v-icon color="error" small @click="deleteItem(item)">delete</v-icon>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    selectedRegion: null,
    search: "",
    regions: [],
    agencies: [],
    dialog: false,
    headers: [
      {
        text: "Agencia",
        align: "left",
        sortable: false,
        value: "agency_name"
      },
      { text: "Region", value: "name" },
      { text: "Horario", value: "schedule" },
      { text: "Ubicación", value: "address" },
      { text: "Referencia", value: "reference" },
      { text: "Actions", value: "action", sortable: false }
    ],
    editedIndex: -1,
    editedItem: {
      agency_name: "",
      region: 1,
      schedule: "",
      address: "",
      reference: ""
    },
    defaultItem: {
      agency_name: "",
      region: 1,
      schedule: "",
      address: "",
      reference: ""
    }
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nueva agencia" : "Editar Agencia";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.initialData();
  },

  methods: {
    initialData() {
      axios
        .get("/api/chatbot/agent/agencies/list")
        .then(res => {
          if (res.data.ok) {
            console.log(res.data);
            this.agencies = res.data.payload;
          } else {
            // error msg
            console.error(res.data);
          }
        })
        .catch(error => {
          console.log(error);
        });
      axios
        .get("/api/chatbot/agent/regions/list")
        .then(res => {
          if (res.data.ok) {
            console.log(res.data);
            this.regions = res.data.payload;
          } else {
            // error msg
            console.error(res.data);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    getRegionName(id) {
      return this.regions.find(region => region.id == id).name;
    },
    editItem(item) {
      this.editedIndex = this.agencies.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.selectedRegion = this.editedItem.region;
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.agencies.indexOf(item);
      confirm("¿Seguro que quieres eliminar esta agencia?") &&
        this.agencies.splice(index, 1);
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.agencies[this.editedIndex], this.editedItem);
      } else {
        console.log("se guardara: ", this.editedItem);
        this.editedItem.region = this.selectedRegion;
        axios
          .post("/api/chatbot/agent/agencies/create", {
            newAgency: this.editedItem
          })
          .then(res => {
            if (res.data.ok) {
              console.log(res.data);
              this.agencies.push(this.editedItem);
            } else {
              // error msg
              console.error(res.data);
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
      this.close();
    }
  }
};
</script>

<style lang="scss" scoped>
</style>