<template>
  <v-container>
    <h1>Listado de Intents de Charly Bot</h1>
    <ul>
      <li v-for="(intent,i) in intents" :key="intent.id">
        <span class="title">Intención {{i+1}} :</span>
        <router-link
          class="link"
          :to="{name:'updateIntent',params:{id:getIntentId(intent.name)}}"
        >{{intent.displayName}}</router-link>
      </li>
    </ul>
  </v-container>
</template>

<script>
import { extractIntentId } from "../../tools/stringFunctions";
import axios from "axios";
export default {
  data() {
    return {};
  },
  computed: {
    intents() {
      return this.$store.state.intents;
    }
  },
  methods: {
    getIntentId(name) {
      return extractIntentId(name);
    },
    hasRichMessage(content) {
      var content = JSON.parse(content);
      for (let i = 0; i < content.length; i++) {
        const element = content[i];
        if (element.type == 4) {
          return true;
        }
      }
      return false;
    }
  }
};
</script>

<style scoped>
.link {
  text-decoration: none;
}
.link:hover {
  text-decoration: underline;
}
</style>